<template>
	<v-card class="body" width="100%" height="100%" tile flat :style="{position:'relative'}">
		<div class="d-flex justify-center pa-5">
			<span><strong>Recovery Phrase</strong></span>
		</div>
		<div v-if="copiedP" class="caption pa-3">
			<v-alert type="success" dense outlined>Phrase copied!</v-alert>
		</div>

		<div class="pa-3 cardx">

			<div class="dvc">
				<v-chip v-for="(item,index) in phrase.slice(0,3)" :key="index" small outlined><span>{{index+1}}</span>&nbsp;<strong>{{item.text}}</strong></v-chip>
			</div>

			<div class="dvc">
				<v-chip v-for="(item,index) in phrase.slice(3,6)" :key="index" small outlined><span>{{index+4}}</span>&nbsp;<strong>{{item.text}}</strong></v-chip>
			</div>

			<div class="dvc">
				<v-chip v-for="(item,index) in phrase.slice(6,9)" :key="index" small outlined><span>{{index+7}}</span>&nbsp;<strong>{{item.text}}</strong></v-chip>
			</div>

			<div class="dvc">
				<v-chip v-for="(item,index) in phrase.slice(9,12)" :key="index" small outlined><span>{{index+10}}</span>&nbsp;<strong>{{item.text}}</strong></v-chip>
			</div>

		</div>

		<div class="d-flex justify-center caption">
			<v-btn @click.stop="copyPhrase" text class="caption blue--text">Copy this phrase key</v-btn>
		</div>

		<div class="cardx v">
			<v-btn @click.stop="login" color="primary" dark tile large width="100%">Done</v-btn>
		</div>
	</v-card>
</template>

<script>
// @ is an alias to /src
//import anime from 'animejs/lib/anime.es.js'
export default {
	name: 'PhrasePageView',
	data:()=>{
		return{
			copiedP:false,
			phrase:[
			{text:'welcome'},{text:'run'},{text:'bell'},
			{text:'drive'},{text:'hope'},{text:'belly'},
			{text:'dosage'},{text:'zigzaggy'},{text:'fun'},
			{text:'lucky'},{text:'like'},{text:'water'},
			]
		}
	},
	components: {
		
	},
	methods:{
		async copyPhrase(){
			
			await navigator.clipboard.writeText(this.$store.getters.get_phrase);
			this.copiedP=true;
			window.setTimeout(this.hideA,3000)
			//CONFIRM
			//this.$router.push('/resetPassword');
		},
		hideA(){
			this.copiedP=false;
		},
		login(){
			this.$router.push('/auth')
		}
	},
	mounted(){
		this.phrase=new Array()
		const phrase=this.$store.getters.get_phrase.split(" ");
		for(let i=0; i<phrase.length; i++){
			this.phrase.push({text:phrase[i]})
		}
	}
}
</script>

<style>
.dvc{
	display: flex;
	justify-content: space-between;
	margin-top: 10px;
}
	@media screen and (max-width: 400px){
		.v{
			position: absolute;
			bottom: .5em;
			width: 100%;
			padding: 7px;
		}
	}
	@media screen and (min-width: 600px){
	.cardx{
			max-width: 400px;
		}
