<template>
	<v-card class="body" color="accent" width="100%" height="100%" tile flat>
		<div class="caption"><v-btn @click="$router.push('/singleasset')" icon><faIcon icon="arrow-left"/></v-btn>Receive {{tag}}</div>
	<div class="body">
		<div class="qrblock1">
			<v-sheet class="pa-3 skeleton" v-show="loader">
			<v-skeleton-loader class="mx-auto" type="card">
				
			</v-skeleton-loader>
			</v-sheet>
			<!--QR CODE HERE -->
			<div class="qrblock2" v-show="add">
			<v-card width="220" class="pa-5 d-flex justify-center mx-auto" v-if="address!==''">
				<qr-code size="180"
				color="#1e88e5"
				:text="address"
				>
				</qr-code>
			</v-card>
			</div>

			<div class="pa-3 caption" :style="{textAlign:'center'}">
					<strong>{{address}}</strong>
			</div>
			<!--<QrCode/>-->
		</div>
		<!--<div class="d-flex justify-center" v-if="address!==''">
			<v-btn @click.stop="checkTransactionState" small rounded color="primary">I have made payment</v-btn>
		</div>-->
		<div class="pa-3 caption d-flex justify-center">
			<span>
				Send {{tag}} to the address or scan the QR Code above
			</span>
		</div>

		<div class="svg">
	<ReceiveImage/>
</div>
</div>
<v-dialog v-model="trxConfirmation" width="250">
	<v-card class="pa-3">
		<div class="caption" :style="{textAlign:'center'}">
			Confirming Transaction
		</div>
		<div class="d-flex justify-center mt-4">
			<v-progress-circular indeterminate></v-progress-circular>
		</div>
	</v-card>
</v-dialog>

<v-dialog v-model="trxConfirmationm" width="250">
	<v-card class="pa-3">
		<div class="caption" :style="{textAlign:'center'}">
			Transaction Successful
		</div>
		<div class="d-flex justify-center mt-4">
			<faIcon color="green" :style="{fontSize:'50px'}" icon="check-circle"/>
		</div>
		<div class="d-flex justify-center mt-4">
			<strong>Amount:{{amount}}&nbsp;{{tag}}</strong>
		</div>
		<div class="d-flex justify-center mt-4">
			<v-btn @click="$router.push('/assetpage')" color="primary" small outlined><faIcon icon="arrow-left"/>close</v-btn>
		</div>
	</v-card>
</v-dialog>

	</v-card>
</template>

<script>
import ReceiveImage from '../components/ReceiveImage.vue'
export default {
	name: 'ReceiveAsset',
	components: {
		ReceiveImage,
	},
	data:()=>{
		return {
			trxConfirmation:false,
			trxConfirmationm:false,
			amount:0,
			size:180,
			add:false,
			loader:false,
			tag:'',
			send:require('../assets/sendcrypto.svg'),
			address:'',//'bc1qucr73v7sc6a32jzuchlvwj5yr88z245jw7efq',tb1qlf37qzlqj2yx8967twxz2272xevtfklq06ktqa
			transaction_id:'',
			check:undefined
		}
	},
	methods:{
		checkTransactionState()
		{
			this.check=window.setInterval(this.initiateCheck,10000)
		},
		initiateCheck(){
			this.trxConfirmation=true;
			this.axios({
				//url:this.$store.getters.get_api+'getAddress/'+this.tag,
				url:this.$store.getters.get_api+'checkTransactionState/'+this.address,
				method:'get',
				headers:{
					"Access-Control-Allow-Origin":"*",
					'Authorization':`Bearer ${this.$store.getters.get_token}`
				}
			}).then((res)=>{
				const data = res.data;
				console.log(data)
				if(!data.error){
					if(data.success)
					{
						window.clearInterval(this.check);
						this.trxConfirmation=false;
						this.trxConfirmationm=true;
						this.amount=data.amount;
					}
				}
				
			}).catch((error)=>{
				return error;
			});

		},
		computePrice(price){
			return new Intl.NumberFormat(this.currency.local,{style:'currency',currency:this.currency.currency}).format(price);
		},
		generateQrAddress(){
			this.loader=true;
			this.add=false;
			this.axios({
				url:this.$store.getters.get_api+'getAddress/'+this.tag,
				//url:this.$store.getters.get_api+'generateDepositAddress/'+this.tag,
				method:'get',
				headers:{
					"Access-Control-Allow-Origin":"*",
					'Authorization':`Bearer ${this.$store.getters.get_token}`
				}
			}).then((res)=>{
				const data = res.data;
				console.log(data)
				if(!data.error){
					this.loader=false;
					this.add=true;
					this.address=data.address;
					this.transaction_id=data.transaction_id;
					
				}
				
			}).catch((error)=>{
				return error;
			});
		},
		
	},
	mounted(){
		//this.$store.dispatch('selected_asset')
		//this.generateQrCode();
		const asset = this.$store.getters.get_selected_asset;
		this.quantity=asset.quantity;
		this.icon = asset.logo;
		this.tag = asset.tag;
		this.generateQrAddress();
	}
}
</script>

