<template>
	<v-card class="body" color="accent" width="100%" height="100%" tile flat>
		<v-card color="primary">
		<div :style="{padding:'.5em'}">
			<div><v-btn @click="$router.push('/assetpage')" icon color="white"><faIcon icon="arrow-left"/></v-btn></div>
			<div><span :style="{color:'white'}">{{computePrice(current_price)}}</span></div>
			<div><v-chip color="white" v-if="parseFloat(ppch)>0" :style="{color:'green'}">+{{parseFloat(ppch).toFixed(2)}}</v-chip>
			<v-chip color="white" v-if="parseFloat(ppch)<0" :style="{color:'red'}">{{parseFloat(ppch).toFixed(2)}}</v-chip></div>
		</div>	

		<div class="d-flex justify-center">
			<v-avatar size="50"><v-img :src="icon"></v-img></v-avatar>
		</div>

		<div class="d-flex justify-center mt-1">
			<span>{{quantity}}&nbsp;{{tag}}</span>
		</div>

		<div class="tx_btns d-flex justify-center">
		<div class="btn_cont" :style="{display:'flex',justifyContent:'space-between',padding:'1em'}">
				<div class="btnc">
					<v-btn @click.stop="$router.push('/sendasset')" class="btn transparent" fab solo><faIcon color="white" icon="arrow-up"/></v-btn>
					<span class="mt-2">Send</span>
				</div>

				<div class="btnc">
					<v-btn @click.stop="$router.push('/receiveasset')" class="btn transparent" fab solo><faIcon color="white" icon="arrow-down"/></v-btn>
					<span class="mt-2">Receive</span>
				</div>

				<div class="btnc">
					<v-btn class="btn transparent" fab solo><faIcon color="white" icon="tag"/></v-btn>
					<span class="mt-2">Buy</span>
				</div>		
			</div>
		</div>
</v-card>
			<v-divider></v-divider>
			<div class="mt-3 d-flex justify-center no-history">
					<NoHistoryImage v-if="transactions.length<1"/>
				</div>
			<div class="pa-2 contx">
				<v-card @click="loadData(item,index)" rounded class="pa-2 mb-1 card" ripple  v-for="(item,index) in transactions" :key="index">
					<div><v-btn v-if="item.type==='Transfer'" icon small><faIcon icon="arrow-down"/></v-btn>
					<v-btn v-if="item.type==='Withdrawal'" icon small><faIcon icon="arrow-up"/></v-btn></div>
					<div :style="{display:'flex',justifyContent:'space-between'}">
						<div><strong class="caption">{{item.type}}</strong></div>
						<!--<div><span class="caption">{{item.date}}</span></div>-->
					</div>
					<div :style="{display:'flex',flexDirection:'row',justifyContent:'space-between'}">
						<span class="caption" v-if="item.type==='Transfer'"><strong>from:</strong>{{(item.ref_add!==undefined)?item.ref_add:'waiting...'}}</span>
						<div :style="{overflowX:'hidden'}" class="caption" v-if="item.type==='Withdrawal'"><strong>to:</strong>{{item.recepient_address}}</div>
						<div class="status">
							<v-chip color="#ff5252" small v-if="item.status===0" outlined>Pending</v-chip>
						</div>
						<div class="status">
							<v-chip small v-if="item.status===1" outlined>Complete</v-chip>
						</div>
					</div>
					<div class="caption">
						<strong>TxID:</strong>&nbsp;{{item.transaction_id}}
					</div>
				</v-card>
			</div>

			<v-dialog v-model="confirmation" width="250" persistent>
				<v-card class="pa-2" :style="{position:'relative'}">
					<v-btn @click.stop="confirmation=!confirmation" icon :style="{position:'absolute',top:'2px',right:'2px'}"><faIcon icon="close"/></v-btn>
					<div class="c_h mt-3" v-if="type==='Withdrawal'">
						<v-chip><faIcon icon="arrow-up"/>&nbsp;{{type}}</v-chip>
					</div>
					<div class="c_h mt-3"  v-if="type==='Transfer'">
						<v-chip><faIcon icon="arrow-up"/>&nbsp;{{type}}</v-chip>
					</div>
					
					<div :style="{overflowX:'hidden'}" class="mt-3 caption d-flex justify-center" v-if="type==='Withdrawal'">
						<strong>to:</strong>{{recepient_address}}</div>
						<div class="mt-3 d-flex justify-center">
							<v-chip color="#ff5252" small v-if="status===0" outlined>Pending</v-chip>

							<v-chip color="green" small v-if="status===1" outlined>Completed</v-chip>
						</div>

						<div class="mt-3 d-flex justify-center">
							Confirmations&nbsp;{{confirmations}}
						</div>
						
				</v-card>
			</v-dialog>
	</v-card>
</template>

<script>
	import NoHistoryImage from '../components/NoHistoryImage.vue'
export default {
	name: 'SingleAsset',
	components: {
NoHistoryImage
	},
	data:()=>{
		return {
			current_price:0,
			icon:require('../assets/bitcoin.png'),
			quantity:2,
			tag:'BTC',
			transactions:[
			/*{id:1,type:'Transfer',quantity:43,ref_add:'9nb6ttfhjHJHFFDcVHHJKJK',date:'Dec 5, 2021'},
			{id:1,type:'Withdrawal',quantity:43,ref_add:'9nb6ttfhjHJHFFDcVHHJKJK',date:'Dec 5, 2021'},
			{id:1,type:'Transfer',quantity:43,ref_add:'9nb6ttfhjHJHFFDcVHHJKJK',date:'Dec 5, 2021'},
			{id:1,type:'Withdrawal',quantity:43,ref_add:'9nb6ttfhjHJHFFDcVHHJKJK',date:'Dec 5, 2021'},
			{id:1,type:'Transfer',quantity:43,ref_add:'9nb6ttfhjHJHFFDcVHHJKJK',date:'Dec 5, 2021'},
			{id:1,type:'Withdrawal',quantity:43,ref_add:'9nb6ttfhjHJHFFDcVHHJKJK',date:'Dec 5, 2021'}*/
			],
			ppch:'+2.34',
			currency:{local:'en-US' , currency:'USD'},
			selected:[],
			asset_icon:'',
			name:'',
			current_page:1,
			total_page:0,
			confirmation:false,
			//SINGLE TRANSACTION
			type:'Withdrawal',
			recepient_address:'',
			generated_address:'',
			status:'',
			confirmations:0
		}
	},
	methods:{
		loadData(item,index){
			let tx = this.transactions[index];
			this.type=tx.type;
			this.recepient_address=tx.recepient_address;
			this.status = tx.status;
			this.confirmations=tx.confirmations
			this.confirmation=true;
		},
		async loadPrice(){
			let chain=new String(this.name).toLowerCase();
				this.axios({
					url:'https://api.coingecko.com/api/v3/simple/price?ids='+chain+'&vs_currencies=USD&include_24hr_change=true',
					method:'get',
					headers:{
					}
				}).then((res)=>{
					const data = res.data;
					console.log(data)
					this.current_price=data[chain].usd;
					this.ppch=data[chain].usd_24h_change;
				
				}).catch((error)=>{
					return error;
				})
				
		},
		getTransactionHistory(page){
			this.axios({
				url:this.$store.getters.get_api+'fetchMyTransactions/'+this.tag+'/'+page,
				method:'get',
				headers:{
                "Access-Control-Allow-Origin":"*",
                'Authorization':`Bearer ${this.$store.getters.get_token}`
            }
			}).then((res)=>{
				
				const data = res.data;
				console.log(data)
				if(!data.error){
					this.transactions=data.message.data;
				}
			}).catch((error)=>{
				return error;
			})
		},
		getBalance(){
			this.axios({
				url:this.$store.getters.get_api+'loadAssetBalance/'+this.tag,
				method:'get',
				headers:{
                "Access-Control-Allow-Origin":"*",
                'Authorization':`Bearer ${this.$store.getters.get_token}`
            }
			}).then((res)=>{
				
				const data = res.data;
				console.log(data)
				if(!data.error){
					this.quantity=data.balance;
				}
			}).catch((error)=>{
				return error;
			})
		},
		computePrice(price){
			return new Intl.NumberFormat(this.currency.local,{style:'currency',currency:this.currency.currency}).format(price);
		}
	},
	mounted(){
		const asset = this.$store.getters.get_selected_asset;
		this.quantity=asset.quantity;
		this.icon = asset.logo;
		this.tag = asset.tag;
		this.name = asset.name
		console.log(this.name)
		//console.log(this.quantity,this.icon,this.tag)
		this.loadPrice();
		this.getBalance();
		this.getTransactionHistory(1);
	}
}
</script>

