			</v-card>
			<div v-if="no_doc===false"><v-card>
				<img :style="{maxWidth:'300px',maxHeight:'250px'}" @click.stop="openSelect" src="" id="kyc_doc">
			
			</v-card></div>
			<v-btn @click="submit" color="primary" class="mt-2">Submit&nbsp;<faIcon icon="user-circle" class="icon"/></v-btn>
		</div>

		<input @change="processImage" id="select_img" type="file" accept="image/*" :style="{visibility:'hidden'}"/>
<v-overlay v-show="processx">		
<v-progress-circular indeterminate></v-progress-circular>
</v-overlay>
<v-dialog persistent v-model="message" width="200" class="pa-3">
	<v-card :style="{position:'relative'}">
		<v-card-title :style="{fontSize:'16px'}">{{message_c}}</v-card-title>
		<v-card-actions class="d-flex justify-center">
			<faIcon v-if="error===true" color="red" class="icon1" icon="times-circle"/>
			<faIcon v-if="error===false" color="green" class="icon1" icon="check-circle"/>
		</v-card-actions>
		<v-btn @click="message=!message" icon :style="{position:'absolute',top:'3px',right:'3px'}"><faIcon color="primary" class="icon" icon="close"/></v-btn>
	</v-card>
</v-dialog>
	</v-card>
</template>

<script>
export default {
	name: 'KycView',
	components: {

	},
	data:()=>{
		return {
			error:undefined,
			message:false,
			message_c:'',
			processx:false,
			no_doc:true,
			firstname:undefined,
			lastname:undefined,
		}
	},
	methods:{
		submit(){
			this.overlay=true;
			const form=new FormData();
			form.append('firstname',this.firstname);
			form.append('lastname',this.lastname);
			form.append('kyc_doc',document.getElementById('select_img').files[0],document.getElementById('select_img').files[0].name);
			this.axios({
				url:this.$store.getters.get_api+'submitKyc',
				method:'post',
				data:form,
				headers:{
					"Access-Control-Allow-Origin":"*",
					'Authorization':`Bearer ${this.$store.getters.get_token}`
				}
			}).then((res)=>{
				this.overlay=false;
				const data = res.data;
				if(!data.error){
					this.error=false;
					this.message_c=data.message;
					this.message=true;
				}
				else{
					this.error=true;
					this.message_c=data.message;
					this.message=true;
				}
			}).catch((error)=>{
				this.error=true;
				this.message_c='Network Error'
				this.message=true;
				return error;
			});
		},
		openSelect(){
			document.getElementById('select_img').click()
		},
		processImage(){
			const reader=new FileReader();
			this.no_doc=false;
			reader.onload=(e)=>{
				document.getElementById('kyc_doc').src=e.target.result;
			}
			reader.readAsDataURL(document.getElementById('select_img').files[0])
		}
		},
	mounted(){
		
	},
	created(){
		
	},

}
</script>

