	},
	methods:{
		updateNotification(){//READ NOTICE
			this.axios({
				url:this.$store.getters.get_api+'updateNotification',
				//url:this.$store.getters.get_api+'generateDepositAddress/'+this.tag,
				method:'get',
				headers:{
					"Access-Control-Allow-Origin":"*",
					'Authorization':`Bearer ${this.$store.getters.get_token}`
				}
			}).then((res)=>{
				const data = res.data;
				if(!data.error){
						return;	
				}
				
			}).catch((error)=>{
				return error;
			});
		}
	},
	watch:{
	}
}
