<template>
	<v-card class="body" color="accent" width="100%" height="100%" tile flat>
		<div class="caption"><v-btn @click="$router.push('/singleasset')" icon><faIcon icon="arrow-left"/></v-btn>Send {{tag}}</div>
		<div>
		<v-card class="pa-3" color="transparent" tile flat>
			<v-card-text :style="{textAlign:'center'}"><strong>Send</strong>&nbsp;{{tag}}</v-card-text>
			<div class="contx">
			<div class="cont2">
			<v-text-field label="Recipient Address" outlined v-model="receiving_address">
				<template v-slot:append>
					<v-btn color="primary" class="mb-3" @click.stop="openScanner" icon><faIcon icon="qrcode"/></v-btn>
				</template>
			</v-text-field>
			<v-text-field type="number" v-model="amount" label="Quantity" outlined></v-text-field>
			<div @click.stop="processt" class="d-flex justify-center"><v-btn color="primary" dark>Continue&nbsp;<faIcon icon="arrow-right"/></v-btn></div>
			</div>
			</div>
		</v-card>
		</div>
		<div class="svg">
	<SendImage/>
</div>
	<v-dialog v-model="scanner" maxWidth="400" persistent>
		<v-card class="primary">
			<qrcode-stream :camera="camera" @decode="onDecode" @init="onInit" />
			<v-card-actions class="d-flex justify-center">
				<v-btn @click.stop="closeScanner" small color="primary" dark fab><faIcon icon="close"/></v-btn>
			</v-card-actions>
		</v-card>
	</v-dialog>

	<v-overlay dark class="d-flex justify-center" v-if="processTransfer">
		<v-card width="450" class="pop pa-3">
			<div :style="{display:'flex',justifyContent:'start'}">
				<div class="caption pa-3"><v-btn @click="processTransfer=!processTransfer" icon><faIcon icon="arrow-left"/></v-btn></div>
			</div>
			
			<div :style="{textAlign:'center'}">
				You are about to send <strong>{{amount}}&nbsp;{{tag}}</strong>
				to the wallet address
				<br/>
				<span class="caption">{{receiving_address}}</span>
				<br/>
				<span class="caption"><strong>!Note:</strong>&nbsp;Network fee will apply</span>
			</div>	
			<v-card-actions class="d-flex justify-center">
				<v-btn @click.stop="sendFund" :loading="send_loading" rounded color="primary">Send&nbsp;<faIcon icon="arrow-right"/></v-btn>
			</v-card-actions>
		</v-card>
	</v-overlay>

	<v-overlay v-if="cam_error" maxWidth="400">
		<v-card :style="{position:'relative'}">
			<v-btn @click.stop="cam_error=!cam_error" icon small fab :style="{position:'absolute'}"><faIcon icon="close"/></v-btn>
			<ScannerImage/>
			<v-card-text>
				{{cam_error_message}}
			</v-card-text>
		</v-card>
	</v-overlay>

	<v-overlay v-if="send_response" maxWidth="400">
		<v-card>
			<v-img v-if="tx_status" :src="send_success"></v-img>
			<v-card-text v-if="tx_status">Transfer request received.</v-card-text>

			<v-img v-if="!tx_status" :src="send_failed"></v-img>
			<v-card-text v-if="!tx_status">{{error_txt}}</v-card-text>

			<v-card-actions class="d-flex justify-center"><v-btn @click="send_response=!send_response" icon color="primary" dark><faIcon icon="close"/></v-btn></v-card-actions>
		</v-card>
	</v-overlay>

	</v-card>
</template>

<script>
import SendImage from '../components/SendImage.vue'
import ScannerImage from '../components/ScannerImage.vue'
import { QrcodeStream } from 'vue-qrcode-reader'
export default {
	name: 'ReceiveAsset',
	components: {
		ScannerImage,
		SendImage,
		QrcodeStream, //QrcodeDropZone,
		//QrcodeCapture
	},
	data:()=>{
		return {
			error_txt:'',
			tx_status:false,
			send_response:false,
			send_loading:false,
			cam_error_message:'',
			cam_error:false,
			myaddress:'',
			processTransfer:false,
			scanner:false,
			tag:'',
			send:require('../assets/sendcrypto.svg'),
			send_success:require('../assets/transfer.png'),
			send_failed:require('../assets/icons8-cancel-200.png'),
			error:'',
			camera:'off',
			amount:0,
			total_amount:0,
			receiving_address:undefined,
			currency:{local:'en-US' , currency:'USD'},
		}
	},
	methods:{
		sendFund(){
			this.send_loading=true;
			const form = new FormData();
			form.append('receipiant' , this.receiving_address);
			form.append('quantity' , this.amount);
			form.append('asset' , this.tag)
			this.axios({
				url:this.$store.getters.get_api+'sendFund',
				method:'post',
				data:form,
				headers:{
				"Access-Control-Allow-Origin":"*",
				'Authorization':`Bearer ${this.$store.getters.get_token}`
				}
			}).then((res)=>{
				const data = res.data;
				this.send_loading=false;
				if(!data.error){
					this.processTransfer=false;
					this.tx_status=true;
					this.send_response=true;
				}
				else{
					this.tx_status=false;
					this.send_response=true;
					this.error_txt=data.message;
				}
			}).catch((error)=>{
				this.send_loading=false;
				this.tx_status=false;
					this.send_response=true;
					this.error_txt='Error In sending request.'
				return error;
			})
		},
		computePrice(price){
			return new Intl.NumberFormat(this.currency.local,{style:'currency',currency:this.currency.currency}).format(price);
		},
		processt(){
			if(this.amount!==0 && this.receiving_address!==undefined)
			{
				this.processTransfer=!this.processTransfer;
			}
		},
		openScanner()
		{
			this.scanner=true;
			this.camera='auto';
		},
		closeScanner()
		{
			this.scanner=false;
			this.camera='off';
		},
		async onInit (promise) {
      try {
        await promise
      } catch (error) {
        if (error.name === 'NotAllowedError') {
          this.cam_error_message = "ERROR: you need to grant camera access permission"
        } else if (error.name === 'NotFoundError') {
          this.cam_error_message = "ERROR: no camera on this device"
        } else if (error.name === 'NotSupportedError') {
          this.cam_error_message = "ERROR: secure context required (HTTPS, localhost)"
        } else if (error.name === 'NotReadableError') {
          this.cam_error_message = "ERROR: is the camera already in use?"
        } else if (error.name === 'OverconstrainedError') {
          this.cam_error_message = "ERROR: installed cameras are not suitable"
        } else if (error.name === 'StreamApiNotSupportedError') {
          this.cam_error_message = "ERROR: Stream API is not supported in this browser"
        } else if (error.name === 'InsecureContextError') {
          this.cam_error_message = 'ERROR: Camera access is only permitted in secure context. Use HTTPS or localhost rather than HTTP.';
        } else {
          this.cam_error_message = `ERROR: Camera error (${error.name})`;
        }
        this.scanner=false;
        this.cam_error=true

      }
    },
		onDecode (decodedString) {
			if(decodedString.length>1)
			{
				this.scanner=false;
				this.camera='off';
				this.receiving_address=decodedString;
			}
		},
	},
	mounted(){
		//this.$store.dispatch('selected_asset')
		//this.generateQrCode();
		const asset = this.$store.getters.get_selected_asset;
		this.quantity=asset.quantity;
		this.icon = asset.logo;
		this.tag = asset.tag;
		//this.generateQrAddress();
	},
	watch:{
		amount(value){
			this.total_amount=this.computePrice(value);
		}
	}
}
</script>

