 <template>
	<v-card class="body" width="100%" height="100%" tile flat>
		<v-card color="primary" dark tile>
			<div class="pt-4 pl-2" :style="{display:'flex',justifyContent:'space-between'}">
				<v-btn @click.stop="$router.push('/noticepage')" icon><v-badge :content="notice" offset-y="18" offset-x="16" color="background"><faIcon class="bell" icon="bell"/></v-badge></v-btn>

				<v-chip large outlined v-if="$store.getters.get_verified===1">
					<v-img :src="verified_logo"></v-img>
