	<v-menu
      :rounded="rounded"
      offset-y
    >
      <template v-slot:activator="{ attrs, on }">
       <v-btn  v-bind="attrs" v-on="on" icon :style="{fontSize:'20px'}"><faIcon icon="ellipsis-v"/></v-btn>
      </template>

        <v-list dense>
        <v-list-item
        v-if="$store.getters.get_verified===0"
        >
          <v-list-item-icon>
            <faIcon icon="handshake-simple"/>
          </v-list-item-icon>

          <v-list-item-content @click.stop="$router.push('/kyc')">
