            <v-list-item-title>Logout</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

      </v-list>
    </v-menu>
					<!---
						--->
			</div>

			<div class="d-flex justify-center pa-3" :style="{fontSize:'20px'}"><strong>{{computePrice(balance)}}</strong></div>
			<div :style="{display:'flex',justifyContent:'center',padding:'1em'}">
				<!--<div class="btnc">
					<v-btn class="btn transparent" fab solo><faIcon icon="arrow-up"/></v-btn>
					<span>Send</span>
