			<div class="asset_cont">
			<v-card @click.stop="loadAssetData(index)" color="accent asset" ripple v-for="(items , index) in assets" :key="index" class="mb-3 pa-1" height="130px" :style="{borderRadius:'10px',fontSize:'13px'}">
				<div :style="{display:'flex',justifyContent:'space-between',padding:'2px'}">
					<span>{{items.name}}</span>
					<div :style="{display:'flex',flexDirection:'column'}">
						<span>{{computePrice(items.current_price)}}</span>
						<span v-if="parseFloat(items.ppch)>0" :style="{color:'green'}">+{{parseFloat(items.ppch).toFixed(2)}}</span>
						<span v-if="parseFloat(items.ppch)<0" :style="{color:'red'}">{{parseFloat(items.ppch).toFixed(2)}}</span>
					</div>
				</div>

