				</div>

				<div :style="{display:'flex',justifyContent:'space-between',padding:'4px'}">
					<span>{{items.quantity}}&nbsp;{{items.tag}}</span>
					<span>{{computePrice(items.price_total)}}</span>
				</div>
			</v-card>
		</div>

		</div>

<v-overlay v-show="logoutp">		
<v-progress-circular indeterminate></v-progress-circular>
</v-overlay>
	
