<script>
export default {
	name: 'AuthView',
	components: {

	},
	data:()=>{
		return {
			rounded:true,
			logoutp:false,
			assets:[
			/*{name:'Smart Chain' , current_price:432.12 , ppch:'+1.23%' , quantity:330 , price_total:142599.6 , logo:require('../assets/5.jpg'), tag:'BNB'},*/
			],
			balance:0,
			currency:{local:'en-US' , currency:'USD'},
			coin_api:'',
			notice:'0',
			loader:true,
			verified_logo:require('../assets/icons8-approval-50.png'),
			
			//unverified_logo:require('../assets/')


		}
	},
	methods:{
		computePrice(price){
			return new Intl.NumberFormat(this.currency.local,{style:'currency',currency:this.currency.currency}).format(price);
		},

		loadAllMyAsset(){
			this.axios({
				url:this.$store.getters.get_api+'loadMyAssets',
				method:'get',
				headers:{
                "Access-Control-Allow-Origin":"*",
                'Authorization':`Bearer ${this.$store.getters.get_token}`
            }
			}).then((res)=>{
				//console.log(this.$store.getters.get_token)
				const data = res.data;
				if(!data.error){
					this.loader=false;
					//console.log(data.tokens)
					/*this.tokens=data.tokens;
					this.token_quantity=data.balance;*/
					this.assets=new Array();
					this.coin_api=data.coin_api_token;
					for(let i=0; i<data.tokens.length; i++){
						this.assets.push({
							name:data.tokens[i].name ,
							current_price:0 , 
							ppch:'0%' , 
							quantity:data.balance[i] , 
							price_total:0 , 
							logo:this.$store.getters.get_api+'assetImage/'+data.tokens[i].icon, 
							tag:data.tokens[i].token_id
						})
					}
					//TRIGGER PROCESS FOR FETCHING TOKENS INFORMATION
					//this.loadAssetIcons();
					this.loadPrice();
				}
			}).catch((error)=>{
				return error;
			})
		},
		async loadPrice(){
			for(let y=0; y<this.assets.length; y++){
				let chain=new String(this.assets[y].name).toLowerCase();
				this.axios({
					url:'https://api.coingecko.com/api/v3/simple/price?ids='+chain+'&vs_currencies=USD&include_24hr_change=true',
					method:'get',
					headers:{
					}
				}).then((res)=>{
					const data = res.data;
					console.log(data)
					this.assets[y].current_price=data[chain].usd;
					this.assets[y].ppch=data[chain].usd_24h_change;
					this.assets[y].price_total=parseFloat(this.assets[y].current_price*this.assets[y].quantity)
					this.balance +=this.assets[y].price_total;
				
				}).catch((error)=>{
					return error;
				})
				
			}
		},
		

		loadAssetData(index){
			this.$store.commit('set_selected_asset' , this.assets[index]);
			//window.localStorage.setItem('selected_asset',this.assets[index])
			this.$router.push('/singleasset')
		},
		logout(){
			this.logoutp=true;
			this.axios({
				url:this.$store.getters.get_api+'logout',
				method:'get',
				headers:{
					"Access-Control-Allow-Origin":"*",
                'Authorization':`Bearer ${this.$store.getters.get_token}`
				}
			}).then((res)=>{
				const data = res.data;
				if(!data.error){
					window.localStorage.setItem('token', undefined);
                      window.localStorage.setItem('verified',undefined)
                      window.localStorage.setItem('whatsapp',undefined)
					this.$router.push('/');
				}
				else{
					this.logoutp=false;
				}
			}).catch((e)=>{
				this.logoutp=false;
				return e;
			})
		}
		
	},
	mounted(){
		
	},
	created(){
		this.loadAllMyAsset();
		this.$store.dispatch('totalNotice');
		let n = this.$store.getters.get_notice;
		//console.log(n.length)
		this.notice=new String(n.length);
		//this.loadPrice();
		/*window.setInterval(()=>{
			if(this.assets.length!==0){
				this.loadPrice();
			}
		},600000)*/
	},

}
</script>

